body,
html {
  margin: 0;
}
img {
  width: 100%;
  height: 100vh;
  display: block;
  filter: brightness(100%);
  /* display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; */
}
.bg1 {
  /* The image used */
  background-image: url("../images/health1.jpg");
  opacity: 0.6;
  /* Full height */
  height: 100%;
  width: 100%;
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.bg2 {
  /* The image used */
  background-image: url("../images/health2.jpg");
  opacity: 0.6;
  /* Full height */
  height: 100%;
  width: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.bg3 {
  /* The image used */
  background-image: url("../images/health3.jpg");
  opacity: 0.6;
  /* Full height */
  height: 100%;
  width: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
/* .customCarousel {
  position: relative;
} */
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.name span {
  color: #00b7ff;
}
.name .details {
  color: #969696;
}
.name h1 {
  font-family: "Montserrat";
  font-size: 40px;
  margin: 200px;
  filter: brightness(100%);
  filter: blur(100%);
  text-align: center;
  letter-spacing: 2px;
  color: #640808;
  text-align: center;
}

.customText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.customDownload{
  position:absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.customText h1 {
  font-family: "Muli", sans-serif;
  font-size: 50px;
  line-height: 1.2;
  font-weight: 900;
  color: #102039;
  text-transform: uppercase;
}
.customText span {
  color: #1f2b7b;
}
.details{
  color: #160546;
  font-family: "Muli", sans-serif;
  font-size: 20px;
  font-weight: bold;
}
.playstore-btn {
  background: url("../images/playstore.png") no-repeat;
  display: inline-block;
  overflow: hidden;

  background-size: 130px 40px;
  width: 130px;
  height: 40px;
}

.appstore-btn {
  background-image: url("../images/appstore.png");
  display: inline-block;
  overflow: hidden;

  border-radius: 10px;
  background-size: 130px 40px;
  width: 130px;
  height: 40px;
}

.appgallery-btn {
  background-image: url("../images/appgallery.png");
  display: inline-block;
  overflow: hidden;

  border-radius: 5px;
  background-size: 130px 40px;
  width: 130px;
  height: 40px;
}
.typical{
  display: inline-block;
}
@media (max-width: 1188px) {
  .customDownload{
    position:absolute;
    top: 75%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 812px) {
  .customDownload{
    position:absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 600px) {
  .customText h1 {
    font-family: "Muli", sans-serif;
    font-size: 50px;
    line-height: 1.2;
    font-weight: 900;
    color: #102039;
    text-transform: uppercase;
  }
  .customText span {
    font-size: 50px;
    color: #1f2b7b;
  }
  .details{
    /* color: #212025; */
    color: #160546;
    font-family: "Muli", sans-serif;
    font-size: 15px;
    font-weight: bold;
  }
  .customDownload{
    position:absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}