#contactContent{
    padding-top: 100px;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 60px;
  height: 600px;
}
table {
  color: #5a5a5a;
}
table a {
  color: inherit;
}
.rightContact td {
  text-align: left;
  /* align-items: center;
    justify-content: center; */
}
@media (max-width: 1080px) {
  .wrapper iframe {
    width: 450px;
    height: 300px;
  }
}
@media (max-width: 860px) {
  .wrapper iframe {
    width: 450px;
    height: 300px;
  }
  .wrapper {
    display: block;
    align-items: center;
    justify-content: center;
    height: 700px;
  }
  .wrapper iframe {
    width: 100%;
  }
  .tableContent {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
}
@media (max-width: 574px) {
  .wrapper {
    display: block;
    align-items: center;
    justify-content: center;
    height: 700px;
  }
  .tableContent {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
}
