input {
  font-family: Arial, Helvetica, sans-serif;
}
textarea {
  font-family: Arial, Helvetica, sans-serif;
}
.formBox {
  position: relative;
  width: 100%;
}

.formBox .row50 {
  display: flex;
  gap: 20px;
}

.inputBox {
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  margin-bottom: 10px;
  width: 100%;
  /* width: 100%; */
}
/* .row50{
    width: 50%;
}
.row100{
    width: 100%;
} */
@media (max-width: 574px) {
  .formBox .row50 {
    display: block;
    gap: 20px;
  }
}
.formBox .row100 .inputBox {
  /* width: 100% ;  */
}
.inputBox label {
  color: #18b7ff;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: 500;
}

.inputBox input {
  padding: 10px;
  font-size: 1.1rem;
  /* outline: none; */
  /* outline-color: #00b7ff; */
  /* outline-style: solid; */
  border: 1px solid #333;
}
.inputBox input:focus {
  /* background-color: #d6f2ff; */
}
.inputBox textarea {
  padding: 10px;
  font-size: 1.1rem;
  outline: none;
  border: 1px solid #333;
  resize: none;
  min-height: 220px;
  margin-bottom: 10px;
}

.inputBox input[type="submit"] {
  background: #ff578b;
  color: #fff;
  border: none;
  font-size: 1.1 rem;
  max-width: 120px;
  font-weight: 500;
  cursor: pointer;
  padding: 14px 15px;
}
.inputBox::placeholder {
  color: #999;
}

.custom-field input {
  border: none;
  appearance: none;
  background: #f2f2f2;
  padding: 12px;
  border-radius: 3px;
  /* width: 250px; */
  width: 100%;
  outline: none;
}
.custom-field textarea {
    border: none;
    appearance: none;
    background: #f2f2f2;
    padding: 12px;
    border-radius: 3px;
    /* width: 250px; */
    width: 100%;
    outline: none;
  }
.custom-field {
  position: relative;
  font-size: 14px;
  padding-top: 20px;
  margin-bottom: 5px;
}
.custom-field .placeholder {
  position: absolute;
  left: 12px;

  top: calc(50% + 10px);
  transform: translateY(-50%);
  color: #aaa;
  transition: top 0.3s ease, font-size 0.3s ease, color 0.3s ease;
}

.custom-field input:valid + .placeholder,
.custom-field input:focus + .placeholder {
  top: 10px;
  font-size: 10px;
  color: #222;
}

.custom-field textarea:valid + .placeholder,
.custom-field textarea:focus + .placeholder {
  top: 10px;
  font-size: 10px;
  color: #222;
}
