}
.appstore-btn-footer {
  background-image: url("../images/appstore.png");
  display: inline-block;
  overflow: hidden;

  border-radius: 7px;
  background-size: 130px 40px;
  width: 130px;
  height: 40px;
}

.appgallery-btn-footer {
  background-image: url("../images/appgallery.png");
  display: inline-block;
  overflow: hidden;

  /* border-radius: 5px; */
  background-size: 130px 40px;
  width: 130px;
  height: 40px;
}