#features {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  font-family: calibri;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  background-color: #ffffff;
}
.a-container {
  display: flex;
  justify-content: center;
  align-content: center;
}

.a-box {
  background-color: #ffffff;
  width: 300px;
  height: 450px;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.1);
  /* transition: 0.3s ease-in-out; */
  position: relative;

  opacity: 0;
  transform: scale(0%);
  /* transform: translateY(5rem); */
  transition: 1s;
}
.a-box--zoom {
  background-color: #ffffff;
  width: 300px;
  height: 450px;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.1);
  /* transition: 0.3s ease-in-out; */
  position: relative;

  opacity: 1;
  transform: scale(100%);
  /* transform: translateY(0rem); */
  transition: 1s;
  /* animation: backToNormal 1s linear;
    animation-delay: 0s; */
}
.a-box--zoom:hover {
  transform: scale(110%);
  z-index: 1;
  transition: 1s;
}
.a-box:hover {
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.1);
}
.a-box {
  flex-grow: 1;
}
.a-b-img {
  width: 100%;
  height: 60%;
}
.a-b-img img {
  padding: 15px;
  width: 100%;
  height: 100%;
}
.s-b-text {
  width: 100%;
  height: 40%;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.s-b-text p {
  margin: 0px;
  /* color: #000266a6; */
  color: #333333a6;
  font-size: 1.1rem;
  font-family: calibri;
  display: block;
  display: -webkit-box;
  max-width: 80%;
  margin-top: 25px;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-bottom: 50px;
}
/* .s-b-text p:hover {
  color: #000266;
} */
.s-b-text a {
  margin-top: 15px;
}
@media (max-width: 1190px) {
  #features {
    height: auto;
    padding-top: 100px;
  }
  .a-container {
    flex-wrap: wrap;
  }
  .a-b-img img {
    object-fit: contain;
  }
}

@media (max-width: 970px) {
  #features {
    margin-bottom: 100px;
  }
}

